/* Button */
.button-group{
  display: inline-flex;
  align-items: center;
  gap: 20px;
  &.lg{
    gap: 40px;
  }
}
.btn {
  padding: $btn-padding-y $btn-padding-x;
  line-height: 1;
  font-size: 16px;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  &-sm{
    padding: $btn-padding-y-sm $btn-padding-x-sm;
    font-size: $btn-font-size-sm;
    border-radius: $btn-border-radius-sm;
    line-height: 1.4;
  }
  &-gray{
    color: $btn-gray-color !important;
  }
  &.btn-with-icon-element{
    display: inline-flex;
    align-items: center;
    position: relative;
    padding-right: 60px;
    .icon-element{
      position: absolute;
      right: 4px;
      top: 50%;
      transform: translateY(-50%);
      width: 36px;
      height: 36px;
      font-size: 18px;
      &:hover{
        background-color: $white;
        color: $primary;
      }
    }
  }
  &-pill{
    border-radius: $btn-pill;
  }
  &-video{
    display: inline-flex;
    align-items: center;
    gap: 10px;
    color: #000;
    font-weight: 500;
    .icon-element{
      width: 36px;
      height: 36px;
      font-size: 18px;
    }
  }
}

//
// Alternate buttons
//
@each $color, $value in $theme-colors {
  .btn-#{$color} {
    background-color: $value;
    border-color: $value;
    color: $white;
    &:hover{
      color: $white;
    }
  }
}



