.section{
  &-content{
    .nav-tabs-wrap{
      margin-top: 30px;
    }
    .section-sub-title{
      margin-bottom: 16px;
    }
    &-header{
      padding-bottom: 50px;
    }
  }
  &-sub-title{
    display: inline-flex;
    align-items: center;
    line-height: 1;
    gap: 8px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    &-inner{
      display: inline-flex;
      align-items: center;
      gap: 8px;
    }
    .icon{
      color: $primary;
    }
    &.separate{
      position: relative;
      display: flex;
      justify-content: center;
      .section-sub-title-inner{
        background-color: $white;
        padding-left: 16px;
        padding-right: 16px;
        z-index: 1;
      }
      &::before{
        height: 1px;
        background-color: $gray-200;
        position: absolute;
        content: "";
        top: 50%;
        left: 0;
        width: 100%;
        transform: translateY(-50%);
      }
    }
  }
  &-img-wrap{
    img{
      width: 100%;
    }
  }
  &-title + .section-text{
    margin-top: 20px;
  }
}
.text-underline-style{
  color: $primary;
  text-decoration: underline;
}