.media{
    display: flex;
    align-items: center;
    gap: 16px;
    .title + .text{
       margin-top: 8px;
    }
}

// post media
.post-media{
    display: flex;
    gap: 16px;
    + .post-media{
        margin-top: 20px;
    }
    &-white{
        h1,h2,h3,h4,h5,h6,
        .post-media-meta{
            color: $white;
        }
        .post-media-meta{
            opacity: 0.6;
        }
    }
    &-title{
        font-size: 18px;
        line-height: 1.4;
        a{
            color: inherit;
            display: block;
            &:hover{
                color: $primary;
            }
        }
    }
    &-meta{
        margin-bottom: 5px;
    }
}