.search{
    &-popup {
        position: fixed;
        left: 0;
        top: 0;
        height: 100vh;
        width: 100%;
        z-index: 999;
        opacity: 0;
        visibility: hidden;
        transition: all 500ms ease;
        &-inner {
            background: $search-popup-inner-bg;
            position: relative;
            padding: 25px 0 30px;
            z-index: 1;
            transform: translateY(-100%);
            transition: all 300ms ease;
        }
        &-header {
            display: flex;
            align-items: center;
            justify-content: center;
            padding-bottom: 15px;
        }
        &-active{
            .search-popup-inner {
                transform: translateY(0);
            }
            .search-popup {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    &-overlay {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background: rgba(0, 0, 0, 0.7);
    }
    &-input-group {
        position: relative;
        border-bottom: 1px solid $border-gray;
        input{
            width: 100%;
            font-size: 24px;
            line-height: 25px;
            color: #333333;
            padding: 14px 14px 14px 0;
            background-color: transparent;
            border: none;
            outline: none;
            &::placeholder{
                font-size: 18px;
                opacity: 0.9;
            }
            &:focus{
                outline: none;
            }
        }
        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            background-color: transparent;
            border: 0;
            font-size: 20px;
            padding: 10px;
            transition: 0.3s all;
            color: $heading-color;
            &:hover{
                color: $primary;
            }
        }
    }
    
}
