.testimonial-section.has-bg{
    position: relative;
    z-index: 1;
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 59%;
        background: url("#{$directory-path}/bg/testimonial-bg.jpg") no-repeat;
        z-index: -1;
    }
}
.testimonial-section {
    position: relative;
    overflow: hidden;
    .line-shape{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        &::after,
        &::before{
            position: absolute;
            content: "";
            width: 96px;
            background-color: $primary;
            transform: rotate(30deg);
            height: 50%;
        }
        &::before{
            top: -63px;
            left: 0;
        }
        &::after{
            right: -20px;
            clip-path: polygon(23% 0%, 99% 0%, 76% 91%, 0% 100%);
            top: 141px;
        }
    }
}