// scss-docs-start container-max-widths
$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1170px
);
// scss-docs-end container-max-widths

@include _assert-ascending($container-max-widths, "$container-max-widths");

$spacer: 0.875rem; // 14px
$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3
);

$grid-gutter-width:           1.75rem; // 28px

$gutters: (
        0: 0,
        1: $spacer * .25, // 3px
        2: $spacer * .5, // 7px
        3: $spacer, // 14px
        4: $spacer * 2, // 28px
        5: $spacer * 3 // 42px
);

