.cta{
  &-section.has-negative{
    position: relative;
    z-index: 2;
    margin-bottom: -60px;
  }
  &-wrap{
    background-color: $primary;
    border-radius: $border-radius-pill;
    padding: 40px;
    width: 1270px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
  }
  &-img{
    position: absolute;
    top: -43px;
    right: -3px;
    width: 164px;
    height: 187px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  &-icon{
    width: 100px;
    height: 100px;
    background-color: $primary;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    position: absolute;
    top: -93px;
    left: 50%;
    transform: translateX(-50%);
  }
}