
.header {
    &-section {
        position: relative;
        z-index: 2;
        &::before{
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            width: 880px;
            height: 150px;
            background: $header-section-before-bg;
            clip-path: polygon(0 0, 90% 0, 70% 100%, 0% 100%);
        }
        &.has-header-main {
           // padding-top: 61px;
        }
        &.has-shape{
            position: relative;
            z-index: 2;
            &::after{
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: url("../../images/shape/header-bg.jpg");
                z-index: -1;
            }
        }
    }
    &-top {
        padding-top: $header-top-padding-y;
        padding-bottom: $header-top-padding-y;
        position: relative;
        z-index: 1002;
        &:after{
            position: absolute;
            z-index: -1;
            content: "";
            top: 0;
            right: 0;
            width: 1766px;
            height: 100%;
            background-color: $header-top-after-bg;
            clip-path: polygon(0 0, 100% 0, 100% 100%, 3% 100%);
        }
    }
    &-top-left{
        width: 80%;
        padding-left: 340px;
    }
    &-top-right {
        width: 20%;
    }
    &-top-contact-info{
        display: flex;
        align-items: center;
        gap: 40px;
        li {
            color: $header-top-li-color;
            display: flex;
            align-items: center;
            gap: 8px;
            a{
                color: inherit;
            }
        }
        .icon {
            color: $header-top-icon-color;
        }
    }
    &-top-right{
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 20px;
        .language-switcher-wrap {
            position: relative;
            z-index: 1002;
            min-width: 110px;
            margin-right: -36px;
            top: 4px;
        }
        .form-choice-wrap.is-transparent {
            .choices__inner{
                color: $white;
            }
            .choices[data-type*='select-one']::after {
                color: $white;
                right: 52px;
            }
            .choices__list--dropdown{
                right: 36px;
                border-radius: 0;
                border-left: 0;
                border-right: 0;
                border-bottom: 0;
                border-top: $dropdown-top-border;
                margin-top: 4px;
            }
        }
    }
    &-logo{
        position: relative;
        z-index: 1;
        flex-shrink: 0;
        padding: 14px;
        &::after{
            position: absolute;
            z-index: -1;
            content: "";
            top: 0;
            left: 0;
            width: 182px;
            height: 100%;
            background: $header-logo-after-bg;
            clip-path: polygon(0 0, 101% 0, 53% 100%, 0% 100%);
            border-top-left-radius: 50px;
            border-bottom-left-radius: 50px;
        }
       &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 127px;
            top: -97px;
            right: 19px;
            box-shadow: 8px 0px 25px -22px rgba(0, 0, 0, 0.2);
            transform: rotate(52deg);
            z-index: -1;
       }
    }
    &-menu-actions{
        margin-left: 40px;
        .icon-list{
            gap: 16px;
        }
        .contact-btn-wrap{
            padding-left: 16px;
        }
        .icon-element{
            position: relative;
        }
        .count-badge{
            position: absolute;
            top: -5px;
            right: -5px;
            border: 1px solid $white;
            width: 17px;
            height: 17px;
        }
    }

}

