.counter{
  &-wrap{
    background: $white;
    padding: 70px 20px 0 0;
    clip-path: polygon(6% 0, 91% 0, 100% 101%, 0% 101%);
  }
  &-card{
    background-color: $gray-100;
    border-radius: 8px;
    text-align: center;
    position: relative;
    border-top: 4px solid $primary;
    @include  transition(0.2s);
    &:hover{
      background-color: $primary;
      @include transform(translateY(-10px));
      color: $white;
      .counter-number{
        color: $white;
      }
      .counter-icon{
        background: $heading-color;
      }
    }
  }
  &-icon{
    width: 74px;
    height: 74px;
    background-color: $primary;
    border-radius: 30px;
    display: grid;
    place-items: center;
    border: 4px solid $white;
    position: absolute;
    top: -36px;
    left: 50%;
    transform: translateX(-50%);
    @include  transition(0.2s);
    img{
      height: 34px;
    }
  }
  &-body{
    padding: 54px 20px 20px 20px;
  }
  &-number{
    font-size: 26px;
    font-weight: 600;
    color: $heading-color;
    margin-bottom: 8px;
    @include  transition(0.2s);
  }
  &-text{
    font-weight: 500;
    font-size: 14px;
  }
}
.funfact-section{
  &.has-bg{
    position: relative;
    z-index: 3;
    &::after{
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url("../../images/bg/counter-bg.jpg") no-repeat;
      background-size: cover;
      z-index: 1;
    }
    .overlay{
      z-index: 2;
    }
    .container{
      position: relative;
      z-index: 3;
    }
  }
}