.icon{
    &-list {
        display: flex;
        align-items: center;
        gap: 8px;
    }
    &-element{
        width: $icon-element-width;
        height: $icon-element-height;
        background-color: $icon-element-bg;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $icon-element-color;
        border-radius: $icon-element-border-radius;
        font-size: 14px;
        border: 0;
        &.has-shadow{
            @include box-shadow(0 2px 5px rgba(0,0,0, 0.1));
        }
        &.gray{
            background-color: $icon-element-bg-gray;
            color: $icon-element-color-gray;
        }
        &.dark{
            background-color: $gray-900;
            color: $white;
        }
        &.white-soft{
            background-color: rgba($white, 0.2);
            color: $white;
        }
        &:hover{
            background-color: $icon-element-hover-bg;
            color: $icon-element-hover-color;
        }
        &.circle{
            border-radius: $icon-element-border-radius-full;
        }
        &.medium{
            width: $icon-element-width-medium;
            height: $icon-element-height-medium;
            font-size: 22px;
        }
        &.large{
            width: $icon-element-width-large;
            height: $icon-element-height-large;
            font-size: 30px;
        }
    }
}

