.hero{
    &-section{
        padding-top: $hero-section-gap-y;
        padding-bottom: $hero-section-gap-y;
        position: relative;
        z-index: 1;
        &.has-bg{
            position: relative;
            z-index: 1;
            &::after{
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: url(../../images/bg/hero-img.jpg);
                background-repeat: no-repeat;
                background-position: top;
                background-size: cover;
                z-index: -1;
            }
        }
    }
    &-badge{
        display: inline-block;
        background: $hero-badge-bg;
        padding: $hero-badge-padding-y $hero-badge-padding-x;
        font-size: $hero-badge-font-size;
        font-weight: 600;
        color: $hero-badge-color;
        position: relative;
        margin-bottom: 30px;
        .icon{
            position: relative;
            top: 3px;
        }
        &::after{
            border-style: solid;
            height: 100%;
            width: 0;
            border-color: transparent transparent transparent $hero-badge-shape-border-color;
            border-width: 21px 0 21px 30px;
            top: 50%;
            content: "";
            right: -30px;
            position: absolute;
            transform: translateY(-50%);
        }
    }
    &-title{
        font-size: $hero-title-font-size;
        color: $hero-title-color;
        line-height: 1.1;
        margin-bottom: 25px;
        &.has-bar{
            position: relative;
            padding-left: 24px;
            span{
                position: absolute;
                top: 50%;
                left: 0;
                width: 4px;
                height: 80%;
                background-color: $primary;
                transform: translateY(-46%);
                &::after{
                    position: absolute;
                    content: "";
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                    width: 100%;
                    height: 50px;
                    background-color: #fff;
                }
            }
        }
    }
    &-text{
        font-size: 17px;
        color: #fff;
        max-width: 570px;
        line-height: 1.8;
        margin-bottom: 34px;
    }
    &-content{
        max-width: 775px;
        &.has-shape{
            position: relative;
            z-index: 1;
            padding: 70px 0 70px 90px;
            &::after{
                position: absolute;
                content: "";
                top: 0;
                left: 0;
                width: 100%;
                height: 544px;
                background: url("../../images/shape/hero-content-shape.png");
                background-position: left;
                background-repeat: no-repeat;
                z-index: -1;
                transform: rotate(-0.5deg);
            }
        }

    }
    &-experience{
        &-wrap{
            position: absolute;
            top: 0;
            right: 0;
            text-align: center;
            .title{
                font-size: 22px;
                font-weight: 600;
                color: $hero-experience-wrap-color;
                margin-top: 14px;
                display: block;
            }
        }
        background-color: $hero-experience-bg;
        padding: $hero-experience-padding-y $hero-experience-padding-x;
        font-size: 70px;
        font-weight: 600;
        color: $hero-experience-color;
        span{
            background-image: url(../../images/bg/experience-img.jpg);
            background-repeat: repeat; 
            -webkit-background-clip: text; 
            -webkit-text-fill-color: transparent;
            background-position: left;
        }
    }
}

@include media-breakpoint-up(xl){
    .hero{
        &-section{
            .container{
                max-width: 1400px;
            }
        }
    }
}