.about-img-wrap{
  position: relative;
  z-index: 2;
  padding-right: 18px;
  &::before{
    position: absolute;
    content: "";
    width: 80%;
    height: 108%;
    background-color: $gray-100;
    top: -4%;
    left: -4%;
    z-index: -1;
  }
  &::after{
    position: absolute;
    content: "";
    bottom: -4%;
    left: -4%;
    width: 200px;
    height: 180px;
    background-color: $primary;
    z-index: -1;
  }
}