.feature-card{
  padding: $feature-card-padding-y $feature-card-padding-x;
  border-right: 1px solid $feature-card-border-right-color;
  border-bottom: 1px solid $feature-card-border-bottom-color;
  position: relative;
  z-index: 1;
  @include transition(0.4s);
  height: 100%;
  &::after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 10px;
    background-color: $primary;
    @include transition(0.4s);
    opacity: 0;
    visibility: hidden;
    z-index: -1;
  }
  &:hover{
    &::after{
      width: 100%;
      height: 100%;
      opacity: 1;
      visibility: visible;
    }
    color: #fff;
    border-right-color: #fff;
    .feature-card{
      &-title{
        color: #fff;
      }
      &-icon img{
        filter: brightness(1000%);
      }
    }
    .btn-gray{
      color: $primary !important;
    }
  }
  &-icon{
    margin-bottom: 30px;
    img{
      @include transition(0.1s);
    }
  }
  &-title{
    margin-bottom: 14px;
    @include transition(0.1s);
  }
  &-text{
    margin-bottom: 18px;
  }
  &-contact{
    background-color: $feature-card-contact-bg;
    padding: 10px;
    height: 100%;
    position: relative;
    &-col{
      width: 180px;
      flex: inherit;
    }
    &-content{
      border: 1px dashed $feature-card-contact-content-border-color;
      height: 100%;
      .icon{
        font-size: 40px;
        color: $feature-card-contact-icon-color;
      }
    }
    &-inner{
      display: flex;
      align-items: center;
      gap: 18px;
      position: absolute;
      top: 44%;
      transform: rotate(-90deg) translateY(-50%);
      width: max-content;
      text-align: center;
      span{
        display: block;
        color: $feature-card-contact-text-color;
        font-weight: 600;
      }
      .title{
        font-size: 17px;
      }
      .text{
        font-size: 22px;
      }
    }
  }
}