.accordion{
  &-style-1{
    .accordion-item{
      margin-bottom: 20px;
      border-radius: $accordion-border-radius;
      &:last-child{
        margin-bottom: 0;
      }
      &:not(:first-of-type) {
        border-top: $accordion-border-width solid $accordion-border-color;
      }
    }
    .accordion-button{
      font-size: 18px;
      font-weight: 500;
      gap: 10px;
      line-height: 1;
      border-top-left-radius: var(--bs-accordion-inner-border-radius);
      border-top-right-radius: var(--bs-accordion-inner-border-radius);
      .accordion-icon-btn{
        .icon{
          transform: rotate(-180deg);
          transition: 0.3s;
        }
      }
      &.collapsed{
        background-color: transparent;
        color: $heading-color;
        .accordion-icon-btn{
          background: $primary;
          border-bottom-right-radius: 4px;
          .icon{
            transform: rotate(0);
          }
        }
      }
      &::after{
        display: none;
      }
      &:not(.collapsed) {
        box-shadow: none;
      }
    }
    .accordion-icon-btn{
      width: 40px;
      height: 100%;
      background: $dark;
      color: #fff;
      position: absolute;
      top: 0;
      right: 0;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      border-top-right-radius: 4px;
    }
  }
}

// accordion media
.accordion{
  &-media{
    .media{
      &-head{
        background: #f2f2f2;
        padding: 12px;
      }
      &-body{
        p{
            font-size: 15px;
        }
      }
    }
  }
}