.section {
    &-space-top,
    &-space {
        padding-top: 100px;
    }
    &-space-bottom,
    &-space {
        padding-bottom: 100px;
    }
    &-space-sm-top,
    &-space-sm {
        padding-top: 50px;
    }
    &-space-sm-bottom,
    &-space-sm {
        padding-bottom: 50px;
    }
}

@each $key, $value in $gutters {
    .g-#{$key},
    .gx-#{$key} {
        --bs-gutter-x: #{$value};
    }
    .g-#{$key},
    .gy-#{$key} {
        --bs-gutter-y: #{$value};
    }
}

