.user-admin{
  &-card{
    background-color: $user-admin-card-bg;
    border-top-left-radius: $user-admin-card-top-left-border-radius;
    border-bottom-left-radius: $user-admin-card-bottom-left-border-radius;
    padding: 10px;
    position: relative;
    .title{
      font-size: 18px;
      font-weight: 600;
      color: #000;
    }
    &-inner{
      display: flex;
      align-items: center;
      gap: 10px;
    }
  }
  &-meta{
    position: absolute;
    top: 25px;
    right: -25px;
    height: 34px;
    background-color: $user-admin-meta-bg;
    color: $user-admin-meta-color;
    width: max-content;
    transform: rotate(-90deg);
    padding: 4px 12px;
  }
}
