.list{
  &-bullet{
    li{
      position: relative;
      padding-left: 20px;
      &:not(:last-child){
        margin-bottom: 4px;
      }
      &::after{
        position: absolute;
        content: "";
        top: 9px;
        left: 0;
        width: 8px;
        height: 8px;
        background-color: $primary;
        border-radius: $border-radius-full;
      }
    }
  }
  &-icon{
    li{
      position: relative;
      padding-left: 24px;
      &:not(:last-child){
        margin-bottom: 6px;
      }
      .icon{
        position: absolute;
        top: 4px;
        left: 0;
      }
    }
    &-white{
      li{
        color: $white;
      }
    }
  }
  &-item{
    >*{
      margin-bottom: 10px;
      a{
        color: inherit;
        a{
          &:hover{
            color: $primary;
          }
        }
      }
    }
    &-white{
      >*{
        color: $white;
        a{
          &:hover{
            color: $primary;
          }
        }
      }
    }
    &-inline{
      display: flex;
      align-items: center;
      >*{
        margin-bottom: 0;
        margin-right: 20px;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
}