.header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    &.has-header-main-s1{
        padding-top: 72px;
    }
    //main
    &-main {
       // position: absolute;
       // top: 0;
       // left: 0;
       // right: 0;
       // z-index: 1000;
       // background: $white;
       // transition: all 0.3s ease;
        &.is-transparent{
            background: transparent;
        }
        &.has-fixed {
            position: fixed;
            padding: 0;
            box-shadow: 0 1px 3px 2px rgba($dark, 0.08);
            width: 100%;
            background: $white;
        }
    }
    &-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-toggle {
        z-index: 1001;
    }
    &-menu{
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        width: 250px;
        background: $white;
        box-shadow: 0 0 12px rgba($dark, 0.2);
        padding: 1.5rem 0;
        transform: translateX(-100%);
        z-index: 1001;
        overflow-x: auto;
        &.mobile-menu {
            transition: transform 0.3s linear;
        }
        &.active {
            transform: translateX(0);
            + .header-overlay {
                opacity: 1;
                visibility: visible;
            }
        }
        &.nav {
            display: block;
        }
    }
    &-overlay {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background: rgba($dark, 0.4);
        opacity: 0;
        visibility: hidden;
        z-index: 1000;
        animation: overlay-fade-in .3s ease 0;
    }
}

//menu
.menu{
    &-item{
        position: relative;
        margin-right: $menu-item-gap-x;
        &:last-child{
            margin-right: 0;
        }
    }
    &-link{
        color: $menu-link-color;
        display: flex;
        align-items: center;
        font-size: 16px;
        padding-top: $menu-link-gap-y;
        padding-bottom: $menu-link-gap-y;
        font-weight: 500;
        .has-sub > &{
            &:after{
                position: relative;
                right: -2px;
                content: $ni-chevron-right;
                font-family: $ni-font;
                transition: transform .3s linear;
                color: $menu-link-arrow-color;
                font-weight: 600;
            }
        }
        &:hover,
        .active > &,
        &.active{
            color: $menu-link-hover-color;
        }
        .active > &{
            &:after{
                transform: rotate(90deg);
            }
        }
    }
    &-sub{
        //display: none;
        padding-top: $menu-sub-gap-y;
        padding-bottom: $menu-sub-gap-y;
        background-color: rgba($dark,.06);
        z-index: 1;
        .menu{
            &-item{
                margin-right: 0;
            }
            &-link{
                padding: $menu-sub-menu-link-y $menu-sub-menu-link-x;
                font-size: 15px;
                font-weight: 400;
            }
            &-sub{
                //padding: $menu-sub-gap-y/4 0;
                //margin: 0 $menu-sub-gap-x;
                //background-color: transparent;
                //border-left:1px solid rgba($dark,.08);
            }
            
        }
        &.active{
            display: block;
        }
    }
}

@include media-breakpoint-up(sm){
    
}
@include media-breakpoint-up(lg){
    .header{
        &-wrap {
            transition: 0.4s ease;
        }
        &-toggle {
            display: none;
        }
        &-menu {
            position: static;
            width: 100%;
            background: transparent;
            box-shadow: none;
            padding: 0;
            transform: none;
            display: flex;
            align-items: center;
            transition: none;
            overflow-x: inherit;
        }
    }
    .menu {
        &-wrap{
            display: flex;
            align-items: center;
        }
        &-list {
            display: flex;
            align-items: center;
            margin-left: auto;
            &-mega {
                width: 50%;
            }
        }
        &-link{
            padding-top: $menu-link-gap-y-desk;
            padding-bottom: $menu-link-gap-y-desk;
            .has-sub > &{
                &:after{
                    transform: rotate(90deg);
                }
            }
            .has-sub .has-sub &{
                &:after{
                    transform: rotate(0);
                    padding-bottom: 0;
                }
            }
        }
        &-item{
            &:hover{
                > .menu-sub{
                    top: 100%;
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        &-sub{
            border-top: $dropdown-top-border;
            display: block !important;
            position: absolute;
            top: 108%;
            background-color: $dropdown-bg;
            min-width: 230px;
            box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.05), -2px 0px 5px 1px rgba(0, 0, 0, 0.05);
            opacity: 0;
            visibility: hidden;
            @include transition(0.2s);
            .menu{
                &-link{
                    &:hover {
                        background-color: $menu-sub-menu-link-hover-bg;
                        color: $menu-sub-menu-link-hover-color;
                    }
                }
                &-sub{
                    left: 102%;
                    top: 0;
                    margin: 0;
                    background-color: $white;
                    padding: $menu-sub-gap-y $menu-sub-gap-x;
                }
                &-list{
                    flex-direction: column;
                    li{
                        width: 100%;
                    }
                }
            }
        }
        &-mega {
            min-width: 460px;
            left: auto;
            right: 0;
            &-row {
                display: flex;
            }
        }
    }
}

@include media-breakpoint-up(xl){
    .menu{
        &-mega {
            min-width: 500px;
            right: 0;
            left: 0;
            @include transform(translateX(-50%));
        }
    }
}

@keyframes overlay-fade-in{
    0%{
        opacity: 0;
    }100%{
        opacity: 1;
    }
}