article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
    display: block;
}
body{
    font-family: $body-font;
    color: $body-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 26px;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

*[hidden] {
    display: none;
}

ol,ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

blockquote,q {
    quotes: none;
}

blockquote::after,
blockquote::before,
q::after,
q::before {
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

h1,h2,h3,.h1,.h2,.h3,h4,.h4,h5,h6,.h5,.h6 {
    color:  $heading-color;
    font-family: $heading-font;
    font-weight: bold;
}
h1,h2,h3,h4,h5,h6,p {
    margin: 0;
}

a {
    outline: 0 none;
    transition: all 0.2s;
}

a:link,a:visited {
    text-decoration: none;
}

a:hover,a:focus,a:active {
    outline: 0;
}

img {
    max-width: 100%;
    height: auto;
}

hr{
    opacity: 0.1;
}

h1,.h1 {
    line-height: 1.4;
}
