.footer-section{
  .container{
    position: relative;
    z-index: 1;
  }
}

.footer{
  &-info{
    background-color: #000;
    padding: 100px 30px 30px 30px;
    .list-icon{
      li{
        margin-bottom: 12px;
      }
    }
  }
  &-logo{
    margin-bottom: 24px;
  }
  &-item{
    padding-top: 116px;
    .footer-title{
      margin-bottom: 24px;
    }
    &-white{
      h1,h2,h3,h4,h5,h6{
        color: $white;
      }
    }
  }
}

// copyright
.copyright{
  &-section{
    padding-top: 12px;
    padding-bottom: 12px;
    &-white{
      p{
        color: $white;
      }
    }
  }
  &-left,
  &-right{
    width: 50%;
  }
  &-right{
    .list-item-inline{
      justify-content: flex-end;
    }
  }
}