// service card
.service-card{
  background-color: #f3f3f3;
  position: relative;
  z-index: 1;
  &::after{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    content: "";
    background: url("../../images/bg/service-card-bg.jpg") no-repeat;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    @include transition(0.3s);
  }
  &-head{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: relative;
    border-bottom: 1px solid $border-gray;
    @include transition(0.3s);
  }
  &-body{
    padding: 20px;
  }
  &-icon{
    background: mix($primary, $white, 15%);
    position: absolute;
    top: 0;
    right: 0;
    height: 101%;
    width: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include transition(0.3s);
  }
  &-count{
    font-size: 40px;
    font-weight: 600;
    position: absolute;
    bottom: 20px;
    right: 54px;
    color: #fff;
    font-family: $heading-font;
    opacity: 0;
    visibility: hidden;
    @include transition(0.3s);
  }
  .title{
    a{
      color: inherit;
      &:hover{
        color: $primary;
      }
    }
  }
  .text{
    margin-bottom: 14px;
  }
  .btn{
    position: absolute;
    bottom: 40px;
    right: -39px;
    transform: rotate(-90deg);
    @include transition(0.3s);
  }
  &:hover{
    .service-card{
      &-head{
        background-color: $dark;
      }
      &-icon{
        background-color: $primary;
        img{
          filter: brightness(1000%);
        }
      }
      &-count{
        opacity: 1;
        visibility: visible;
        bottom: 24px;
      }
    }
    .title{
      color: #fff;
    }
    .btn-dark{
      background-color: $primary;
      border-color: $primary;
    }
    &::after{
      opacity: 1;
      visibility: visible;
    }
  }
}

// team card
.team-card{
  text-align: center;
  .icon-list{
    justify-content: center;
  }
  &-img-wrap{
    position: relative;
    z-index: 1;
    &::before{
      position: absolute;
      content: "";
      bottom: 0;
      left: 50%;
      width: 160px;
      height: 20px;
      background-color: $gray-900;
      transform: translateX(-50%);
    }
    &::after{
      position: absolute;
      content: "";
      bottom: -54px;
      left: 50%;
      transform: translateX(-50%);
      width: 160px;
      height: 160px;
      border-radius: 100%;
      background-color: mix($primary, $white, 10%);
      z-index: -1;
      transition: 0.2s;
    }
    img{
      width: auto !important;
      height: 200px;
      margin-left: auto;
      margin-right: auto;
    }
  }
  &-body{
    position: relative;
    z-index: 1;
    padding: 20px 20px 30px 20px;
    .title{
      margin-bottom: 4px;
    }
    .meta{
      display: block;
      margin-bottom: 20px;
    }
    &::after{
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      background-color: $gray-100;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      z-index: -1;
      clip-path: polygon(20% 0, 80% 0, 100% 100%, 0% 100%);
    }
  }
  &:hover{
    .team-card-img-wrap{
      &:after{
        background-color: $primary;
      }
    }
  }
}

// portfolio card
.portfolio-card{
  position: relative;
  &::before,
  &:after{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    visibility: visible;
    @include transition(0.4s);
  }
  &::before{
    transform: scale(0.5);
    height: 5px;
    background: $primary;
    z-index: 1;
  }
  &::after{
    height: 100%;
    background: rgba($dark, 0.5);
  }
  &-body{
    position: absolute;
    bottom: 10px;
    left: 20px;
    right: 20px;
    background: $primary;
    color: $white;
    border-radius: $border-radius;
    padding: 20px;
    @include transition(0.4s);
    opacity: 0;
    visibility: visible;
    z-index: 1;
    .title{
      margin-bottom: 4px;
      color: $white;
    }
    .portfolio-card-icon{
      position: absolute;
      bottom: -14px;
      right: 20px;
      border: 1px solid transparent;
      &:hover{
        border-color: $white;
      }
    }
  }
  &:hover{
    &::before,
    &::after,
    .portfolio-card-body{
      opacity: 1;
      visibility: visible;
    }
    &::before{
      transform: scale(1);
    }
    .portfolio-card-body{
      bottom: 20px;
    }
  }
}

// testimonial card
.testimonial-card{
  position: relative;
  z-index: 1;
  padding: 16px;
  text-align: center;
  &::after{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 180px;
    content: "";
    background-color: $testimonial-card-after-bg;
    z-index: -1;
    @include transition(0.3s);
  }
  .text{
    padding-top: 20px;
    padding-bottom: 20px;
    font-style: italic;
  }
  .title{
    margin-bottom: 6px;
  }
  &-content{
    position: relative;
    padding: 80px 30px 0 30px;
    z-index: 1;
    background-color: $testimonial-card-content-bg;
    &::after{
      position: absolute;
      content: "";
      bottom: -102px;
      left: 0;
      width: 100%;
      background: url("#{$directory-path}/bg/testimonial-card-bg.png") no-repeat;
      z-index: -1;
      height: 102px;
    }
  }
  &-quote{
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    background-color: $testimonial-card-quote-bg;
    border-radius: 100%;
    font-size: 40px;
    display: flex;
    align-items: self-end;
    justify-content: center;
    padding: 10px;
    color: $testimonial-card-quote-color;
    width: 100px;
    height: 100px;
    @include transition(0.3s);
    &::after{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 50%;
      content: "";
      background-color: $testimonial-card-quote-after-bg;
      @include transition(0.3s);
    }
  }
  &-author{
    width: 100px;
    height: 100px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 120px;
    border-radius: 100%;
    img{
      width: 100%;
      border-radius: inherit;
    }
  }
  &:hover{
    .testimonial-card-quote{
      background-color: $testimonial-card-quote-hover-bg;
      color: $white;
      &::after{
        background-color: $testimonial-card-quote-after-hover-bg;
      }
    }
    &::after{
      background-color: $testimonial-card-after-hover-bg;
    }
  }
}


// working card
.working-card{
  border: 1px solid $border-gray;
  border-left: 0;
  position: relative;
  padding: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  &-video{
    width: 700px;
    position: relative;
    &-icon{
      position: absolute;
      top: 30px;
      right: 30px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &-value {
    font-size: 75px;
    font-weight: 600;
    position: absolute;
    top: 140px;
    right: 40px;
    color: transparent;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    -webkit-text-stroke: 1px $gray-200;
    @include transition(0.1s);
  }
  &-icon{
    margin-bottom: 30px;
  }
  .title{
    margin-bottom: 12px;
  }
  .text{
    margin-bottom: 26px;
  }
  &:hover{
    .working-card-value{
      -webkit-text-stroke-color: $gray-900;
    }
    .btn-gray{
      background-color: $primary;
      color: $white !important;
    }
  }
}

// blog card
.blog-card{
  position: relative;
  @include transition(0.2s);
  border: 1px solid $blog-card-border-color;
  .title{
    margin-bottom: 10px;
    a{
      color: inherit;
      &:hover{
        color: $blog-card-title-color;
      }
    }
  }
  &-head{
    padding: 10px 10px 0 10px;
    img {
      width: 100%;
    }
    &-inner{
      position: relative;
      &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $dark;
        opacity: 0;
        @include transition(0.2s);
      }
    }
  }
  &-cat{
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 12px;
    text-transform: uppercase;
    background-color: $blog-card-cat-bg;
    border-radius: $blog-card-cat-border-radius;
    line-height: 1;
    padding: 6px 14px;
    font-weight: 500;
    @include transition(0.2s);
    z-index: 1;
  }
  &-body{
    padding: 20px;
  }
  &-meta{
    background-color: $blog-card-meta-bg;
    line-height: 1;
    padding: 4px 8px 4px 20px;
    font-size: 15px;
    position: relative;
    margin-left: -21px;
    @include transition(0.2s);
    &::after{
      position: absolute;
      content: "";
      right: -11px;
      top: 0;
      width: 0;
      height: 0;
      border-top: 11px solid transparent;
      border-bottom: 12px solid transparent;
      border-left: 11px solid $blog-card-meta-after-color;
      @include transition(0.2s);
    }
  }
  &-comment{
    font-size: 15px;
  }
  &-meta-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  &-footer{
    border-top: 1px solid $blog-card-footer-border-color;
    padding: 12px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &:hover{
    background-color: $blog-card-hover-bg;
    .btn-gray{
      background-color: $primary;
      color: $white !important;
    }
    .blog-card{
      &-cat{
        background-color: $blog-card-cat-hover-bg;
        color: $blog-card-cat-hover-color;
      }
      &-meta{
        background-color: $blog-card-meta-hover-bg;
        color: $blog-card-meta-hover-color;
        &::after{
          border-left-color: $blog-card-meta-after-hover-color;
        }
      }
      &-head-inner{
        &:after{
          opacity: 0.5;
        }
      }
    }
  }
}

@media (min-width: 1400px) {
  .working-card-video {
    width: 350px;
  }
}






