.overlay{
  background-color: #060a20;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.is-dark{
  .section-sub-title,
  .section-title,
  .media .title,
  .media .text {
    color: $white;
  }
}

// faq
.faq-img-wrap{
  img{
    height: 544px;
    object-fit: cover;
  }
}

// skill progress
.skill-wrap{
  padding: 40px 20px 40px 20px;
}
.skill-progress-item{
  text-align: center;
  .title{
    margin-top: 12px;
  }
  input{
    color: $heading-color !important;
    font-family: $heading-font !important;
  }
}

// filter
.filter{
  &-button-group{
    display: flex;
    align-items: center;
    gap: 10px;
    button{
      background-color: transparent;
      border: 0;
      font-weight: 500;
      padding: 6px 14px;
      line-height: 1;
      @include transition(0.2s);
      &.is-checked{
        background-color: $primary;
        color: $white;
      }
    }
  }
  &-container{
    display: flex;
    flex-wrap: wrap;
    margin-left: -14px;
    margin-right: -14px;
    .filter-item{
      width: 20%;
      padding-left: 14px;
      padding-right: 14px;
    }
  }
}

// back to top
.back-to-top {
  position: fixed;
  right: 40px;
  bottom: 40px;
  height: 40px;
  width: 40px;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px rgba(160, 160, 160, 0.3);
  z-index: 1024;
  transform: translateY(20px);
  transition: all 200ms linear;
  opacity: 0;
  visibility: hidden;
  &::after{
    position: absolute;
    content: $ni-arrow-long-up;
    font-family: $ni-font;
    text-align: center;
    font-size: 20px;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    z-index: 1;
    transition: all 200ms linear;
    align-items: center;
    justify-content: center;
  }
  svg {
    path {
      fill: none;
      stroke: $primary;
      stroke-width: 4;
      transition: all 200ms linear;
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    &::after{
      color: $primary;
    }
  }
}

// rating  
.rating{
  display: inline-flex;
  align-items: center;
  gap: 6px;
  &.sm{
    gap: 3px;
    li{
      font-size: 14px;
    }
  }
  &-wrap{
    display: flex;
    align-items: center;
    gap: 8px;
    &.sm{
      gap: 6px;
      .rating-value{
        font-size: 14px;
      }
    }
  }
  li{
    font-size: 16px;
    color: $rating-color;
    line-height: 1;
    &.checked{
      color: $rating-checked-color;
    }
  }
  &-value{
    color: $primary;
  }
}

// pulse anim
.pulse-anim {
  position: relative;
  z-index: 1;
  &::after,
  &::before{
    position: absolute;
    content: '';
    top: -20px;
    left: -20px;
    bottom: -20px;
    right: -20px;
    background-color: rgba(255,255,255,.2);
    border-radius: 50%;
    animation: play-button-pulse 1.8s linear infinite;
    opacity: 0;
    z-index: -1;
  }
  &::after{
    animation-delay: .6s;
  }
}

@keyframes play-button-pulse {
  0% {
    transform: scale(.5);
    opacity: 0
  }

  50% {
    opacity: 1
  }

  100% {
    transform: scale(1.2);
    opacity: 0
  }
}