.skill-wrap.has-bg{
    position: relative;
    z-index: 1;
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("../../images/bg/working-bg.png") no-repeat;
        background-size: cover;
        z-index: -1;
    }
}

// team section bg
.team-section.has-bg{
    position: relative;
    z-index: 1;
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("../../images/bg/team-bg.png") no-repeat;
        z-index: -1;
    }
}
// footer section bg
.footer-section.has-bg{
    position: relative;
    z-index: 1;
    &::before,
    &::after{
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    &::before{
        background-color: $dark;
        opacity: 0.9;
    }
    &::after{
        background: url("../../images/bg/footer-bg.jpg") no-repeat;
        z-index: -1;
        background-size: cover;
    }
}
.footer-shape{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 6px;
    background: url("../../images/shape/footer-shape.png") no-repeat;
    background-size: cover;
}